#sobre{
	width: 100%;
	height: 100%;
	display:-ms-flexbox;
	display:flex;
	margin: 0 auto;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: auto;
	padding: 50px 0 50px 0;
}
.sobre-1{
	width: 70%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: start;
	    align-items: flex-start;
	color:#fff;
}
.sobre-item:nth-child(1){
	font-size: 50px;
}
.sobre-item:nth-child(3){
	font-size: 20px;
}
li {
	list-style-type:none;
	font-weight: bold;
	font-style: italic;
}
.sobre-2{
	width: 30%;
	height: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color: #fff;
}
.sobre-2>img{
	width: 100%;
	border-radius: 50%;
}



