#footer{
	width: 100%;
	height: 120px;
	background: darkgreen;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin-bottom: 0px;
	color: #fff;
	padding: 5px 0;
}
.footer-1{
	width: 50%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: baseline;
	    align-items: baseline;
	-ms-flex-pack: center;
	    justify-content: center;
}
.footer-1 > a{
	font-size: 60px;
	color: #fff;
	-webkit-transition: 1s;
	-o-transition: 1s;
	transition: 1s;
	margin: 0 5px;
}
/*.footer-1 > a:hover{
	transition: .3s;
	transform:scale(1.5);
}*/
.footer-2{
	font-size: 10px;
}


