#contato{
	width: 100%;
	height: 85%;
	display: -ms-flexbox;
	display: flex;
	justify-items: stretch;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 0 auto;
	border: 2px solid yellow;
}
.item-contato{
	margin: 15px 15px;
	border: 0px solid yellow;
	white-space: nowrap;
}
.item-contato:nth-child(1){
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: start;
	    align-items: flex-start;
	font-size: 20px;
}
.item-contato:nth-child(1) > h2{
	font-size: 50px;
}
.item-contato:nth-child(2){
	width: 50%;
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: stretch;
	    align-items: stretch;
}
input{
	height: 50px;
}
input, textarea{
	color: #fff;
	background: rgba(255,50,0,1);
	-webkit-box-shadow: none;
	        box-shadow: none;
	border:none;
	width: 150%;
}
input:focus, textarea:focus{
	border: 1px solid #aaa;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
	color: #ccc;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder{
	color: #ccc;
}
input::-ms-input-placeholder, textarea::-ms-input-placeholder{
	color: #ccc;
}
input::placeholder, textarea::placeholder{
	color: #ccc;
}
form{
	width: 60%;
	border: 0px solid green;
}
.icon{
	margin: 0 10px 0 0;
}
.btn{
	width: 150px;
	border: 1px solid #fff;
	font-size: 20px;
	font-weight: bold;
	background: #f00;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
	margin-left: 20px;
}
.btn:hover{
	background: #fff;
	color: red;
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
}
.item-contato:nth-child(1) > h4 > a{
	text-decoration: none;
	color: #fff;
}
.item-contato:nth-child(1) > h4 > a:hover{
	color: #bbb;
}


